$h1Size: 30px;
$pSize: 14px;

@mixin button {
    .buttonContainer {
        display: flex;
        align-items: center;
        a {
            color: black;
            img {
                margin-right: 10px;
                width: 17px;
                height: 9px; } }
        a {
            h1 {
                font-size: 14px; } } } }
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
@mixin firstBlock {
    max-width: 100%;
    .firstBlock {
        h1 {
            font-size: $h1Size; }
        p {
            font-size: $pSize;
            line-height: 20px; } } }
@font-face {
    font-family: 'Helvetica Neue';
    src: url('../fonts/Helvetica65Medium22443.woff') format('woff');
    font-weight: normal;
    font-style: normal; }
html {
 }    // height: 100%
body {
    font-family: 'Bebas Neue';
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    flex-grow: 1;
    align-items: center;
    p {
        font-family: 'Helvetica Neue';
        font-size: 12px; } }
a {
    text-decoration: none; }
.wrapper {
    max-width: 940px; }
.topLine {
    height: 7px;
    width: 100%;
    background-color: #e2e2e2; }
header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 50px;
    flex: 0 0 auto;
    .header__logo {
        color: #474747;
        font-family: 'Pacifico';
        font-size: 42px;
        margin-top: 30px {
 } }            // line-height: 10px
    nav {
        .topnav {
            display: flex;
            .menuGroup {
                display: flex;
                flex-direction: column;
                &.icon {
                    display: none; }
                .triangleDown {
                    border: 10px solid transparent;
                    border-top: 10px solid #ebebeb;
                    margin: 0 auto 20px;
                    visibility: hidden; }
                a {
                    color: #bfbfbf;
                    font-size: 15px;
                    white-space: nowrap;
                    &:last-child {
                        color: #474747;
                        font-size: 26px; } } }
            .menuGroup:not(:nth-of-type(5)) {
                margin-right: 55px; }
            .menuGroup:hover .triangleDown {
                visibility: visible; } } } }
main {
    flex: 1 0 auto;
    section.swiper-container {
        position: relative;
        .swiper-wrapper {
            .swiper-slide {
                display: flex;
                position: relative;
                img {
                    width: 100%; }
                .left-block {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    color: white;
                    background-color: #444444;
                    width: 217px;
                    height: 72px;
                    padding: 10px 17px;
                    p {
                        font-family: 'Bebas Neue';
                        text-transform: uppercase;
                        &:first-child {
                            color: #9a9a9a;
                            font-size: 17px; }
                        &:last-child {
                            color: #ffffff;
                            font-size: 34px; } } }
                .triangle-up {
                    position: absolute;
                    border: 12px solid transparent;
                    border-bottom: 12px solid #f7f7f7;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%); } } }
        .swiper-pagination {
            display: flex;
            justify-content: flex-end;
            padding-right: 15px;
            padding-bottom: 5px;
            &-bullet {
                border-radius: 0;
                opacity: 1;
                background-color: #fff;
                width: 23px;
                height: 5px; }
            &-bullet-active {
                background-color: #444444;
                // width: 23px
 } } }                // height: 5px
    section.description {
        background-color: #f7f7f7;
        padding-top: 10px;
        h1 {
            font-size: 19px;
            line-height: 21px;
            padding: 0 0 10px 16px; }
        p {
            padding: 0 0 15px 15px;
            font-size: 14px; } }
    section.projects {
        margin: 66px 0 0 0;
        display: grid;
        grid-template-columns: repeat(3, auto);
        column-gap: 45px;
        .projects-top {
            display: flex;
            align-items: center;
            h1 {
                font-size: 25px;
                line-height: 25px; }
            img {
                margin-right: 33px; } }
        p {
            font-family: 'Helvetica Neue';
            font-size: 14px;
            line-height: 17px;
            margin-top: 30px; }
        .projects-bottom {
            margin-top: 10px;
            @include button; } }
    section.works-separator {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 65px 0px 0 0;
        overflow: hidden;
        img {
            height: 29px; }
        h1 {
            font-size: 22px;
            padding: 0 30px 0 20px;
            white-space: nowrap; } }
    section.works {
        display: grid;
        margin: 52px 0 0 0;
        grid-template-columns: repeat(4, auto);
        column-gap: 20px;
        .worksContainer {
            .work-top {
                h1 {
                    margin: 10px 0 0 0;
                    font-size: 18px; }
                p {
                    margin: 10px 0 0 0;
                    line-height: 18px; } }
            .works-bottom {
                margin-top: 10px;
                @include button; } } } }
main.about {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    flex: 1 0 auto;
    @include firstBlock;
    section.separator {
        img {
            100% {}
            height: 5px; } }
    section.aboutContainer {
        max-width: 100%;
        // border: 3px solid orange
        display: grid;
        margin: 30px 0 0 0;
        grid-template-columns: repeat(3,300px);
        grid-gap: 20px;
        .aboutBlock {
            display: flex;
            flex-direction: column;
            width: 100%;
            h1 {
                font-size: 22px; }
            p {
                font-size: 12px;
                margin-top: 22px;
                line-height: 19px;
                &:nth-of-type(1) {
                    color: #9e9e9e; } } } }
    section.aboutContainerStaff {
        display: grid;
        grid-template-columns: repeat(3,300px);
        grid-gap: 20px;
        margin: 100px 0 0 0;
        .aboutStaffBlock {
            border: 0.5px solid #f8f8f8;
            padding-bottom: 25px;
            .blockLeftRight {
                display: flex;
                flex-direction: row;
                padding: 5px 0 0 15px;
                .left {
                    h1 {
                        &:nth-of-type(2) {
                            color: #9e9e9e;
                            font-size: 13px; } } }
                .right {
                    margin-left: auto;
                    font-size: 10px;
                    padding: 10px 20px 0 0; } }
            p {
                padding: 15px 0 0 20px;
                line-height: 17px;
                font-size: 14px; } } } }
main.projects,main.blog,main.contact {
    display: flex;
    flex: 1 0 auto;
    section.leftBlock {
        @include firstBlock;
        section.separator {
            img {
                width: 658px;
                height: 5px; } }
        .toggles {
            margin: 14px 0 0 -2px;
            span {
                // display: block
                font-size: $h1Size - 13;
                margin-right: 10px;
                &:nth-of-type(1) {
                    color: #9e9e9e; } }
            button {
                border: none;
                background: none;
                color: #424146;
                cursor: pointer;
                font-size: 16px;
                margin: 20px 5px;
                &:after {
                    content: ' |'; }
                &:last-child:after {
                    content: ''; } } }
        .posts {
            display: grid;
            margin: 25px 0 0 -1px;
            grid-template-columns: repeat(3, 209px);
            grid-gap: 18px;
            .post {
                border: 1px solid #f8f8f8;
                max-width: 209px;
                padding-bottom: 25px;
                &:hover .darkned::before,&:hover .darkned img:nth-of-type(2),&:hover .darkned img:nth-of-type(3) {
                    display: inherit; }
                .darkned {
                    position: relative;
                    img {
                        &:nth-of-type(1) {
                            position: relative; }
                        &:nth-of-type(2),&:nth-of-type(3) {
                            display: none;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            z-index: 2; } } }
                .darkned::before {
                    display: none;
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    background-color: #000000;
                    opacity: 0.75;
                    z-index: 1; }
                h1 {
                    padding: 6px 0 0 15px;
                    font-size: 20px;
                    &:nth-of-type(2) {
                        margin: -7px 0 0 0;
                        color: #9e9e9e;
                        font-size: 15px; } }
                p {
                    margin: 18px 0 0 14px;
                    max-width: 179px;
                    line-height: 15px; } } }
        .blockBlog {
            margin: 35px 0 0 0;
            h1 {
                font-size: 20px; }
            img {
                margin: 16px 0 0 0;
                max-width: 100%;
                object-fit: cover; }
            .blockBlogContainer {
                display: flex;
                margin: 20px 0 56px 0;
                .blockBlogLeft {
                    h1 {
                        font-size: 15px;
                        &:nth-of-type(2) {
                            margin: 10px 0 0 0; }
                        &:nth-of-type(3) {
                            margin: 15px 0 0 0; } }
                    p {
                        margin: 5px 0 0 0;
                        white-space: nowrap; } }
                .blockBlogRight {
                    max-width: 500px;
                    margin: 5px 0 0 70px;
                    p {
                        font-size: 12px;
                        line-height: 18px;
                        &:nth-of-type(2) {
                            margin: 20px 0 0 0; } } } } }
        .contactText {
            p {
                max-width: 657px;
                &:nth-of-type(1) {
                    margin: 30px 0 0 0; }
                &:nth-of-type(2) {
                    margin: 20px 0 0 0; } } }
        .contactForm {
            margin: 44px 0 0 0;
            p {
                margin: 0 0 12px 0;
                input,textarea {
                    border: 1px solid #eeeeee;
                    background-color: #fafafa;
                    font-family: 'Bebas Neue';
                    padding: 0 0 0 13px;
                    font-size: 14px;
                    &::placeholder {
                        color: #c6c6c6; } }
                input {
                    width: 216px;
                    height: 33px;
                    font-weight: 400;
                    line-height: 28px; }
                textarea {
                    width: 492px;
                    height: 203px;
                    padding: 10px 0 0 13px;
                    resize: none; } }
            @include button; }

        section.clickerImgs {
            margin-top: 74px;
            img {
                margin-right: 7px; } } }
    section.rightBlock {
        margin: 58px 0 0 55px;
        max-width: 218px;
        h1 {
            font-size: 15px;
            &:nth-of-type(2) {
                margin-top: 20px; }
            &:nth-of-type(3) {
                margin: 21px 0 0 0; } }
        p {
            margin-top: 17px;
            font-size: 12px;
            &:nth-of-type(2) {
                margin: 36px 0 0 0; }
            &:nth-of-type(4) {
                margin: 0 0 0 0; }
            &:nth-of-type(5) {
                margin: 23px 0 0 0; }
            &:nth-of-type(6) {
                margin: 5px 0 0 0; }
            span {
                color: #9e9e9e; } } } }
footer {
    width: 100%;
    // flex: 0 0 auto
    .footer-line {
        width: 100%;
        background: url("../img/footer_Line.png");
        background-repeat: repeat-x;
        height: 7px;
        margin-top: 65px;
        z-index: 1; }
    .footer-container {
        display: flex;
        height: 70px;
        justify-content: center;
        align-items: center;
        background: #f8f8f8;
        margin: -3px 0 0 0;
        .footer-left,.footer-right {
            p {
                font-size: 13px;
                white-space: nowrap; } }
        .footer-right {
            p {
                margin-left: 438px; } } } }
@import "media";
